.navigation {
    &__toggle {
        display: none; }
    &__label {
        background: $color-white;
        height: 8rem;
        width: 8rem;
        border-radius: 50%;
        position: fixed;
        top: 10rem;
        right: 10rem;
        z-index: 200;
        text-align: center;
        cursor: pointer; }
    &__back {
        height: 6rem;
        width: 6rem;
        border-radius: 50%;
        background: radial-gradient($color-brown-color, $color-brown-darck);
        position: fixed;
        top: 11rem;
        right: 11rem;
        z-index: 100;
        transition: all 0.7s; }

    &__nav {
        height: 100vh;
        width: 100%;
        position: fixed;
        top: 0;
        right: 0;
        z-index: 150;
        opacity: 0;
        width: 0;
        transition: all .7s; }
    &__list {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }
    &__item {
        text-align: center;
        &:not(:last-child) {
            margin-bottom: 5rem; } }
    &__link {
        font-weight: 300;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        color: $color-white;
        text-transform: uppercase;
        background: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white 50%);
        background-size: 220%;
        padding: 1rem 2rem;
        transition: all .5s;
        display: inline-block;
        &:hover {
            background-position: 100%;
            color: $color-brown-darck;
            transform: translateX(1.5rem); } }
    &__toggle:checked ~ &__back {
        transform: scale(150); }
    &__toggle:checked ~ &__nav {
        width: 100%;
        opacity: 1; }
    &__icon {
        position: relative;
        margin-top: 3.7rem;
        &,
        &::before,
        &::after {
            width: 3rem;
            height: .2rem;
            background: $color-black;
            display: inline-block; }
        &::before,
        &::after {
            content: "";
            position: absolute;
            left: 0;
            transition: all .2s; }
        &::before {
            top: -.8rem; }
        &::after {
            top: .8rem; } }
    &__label:hover &__icon::before {
        top: -1rem; }
    &__label:hover &__icon::after {
        top: 1rem; }
    &__toggle:checked + &__label &__icon {
        background: transparent; }
    &__toggle:checked + &__label &__icon::before {
        top: 0;
        transform: rotate(135deg); }
    &__toggle:checked + &__label &__icon::after {
        top: 0;
        transform: rotate(-135deg); } }
