html {
    font-size: 62.5%; //1единица rem = 10px
    @include respond(tab-land) {
        font-size: 56.25%; }
    @include respond(tab-port) {
        font-size: 50%; }
    @include respond(phone) {
        font-size: 30%; }
    @include respond(big-screen) {
        font-size: 75%; } }

body {
    padding: 5rem;
    background-color: $color-white; }


.container {
    background-color: #f7f7f7;
    position: relative;
    z-index: 0; }


